        </div>
      </div>
      <br>
      <div class="container-fluid">
        <div class="row row-cols-4">
          <template v-for="(group, index) in groupedMedications" :key="index">
            <div class="col">
              <ul class="list-group med-container shadow shadow-lg">
                <li v-for="medication in group" :key="medication.id"
                    :class="{ med_selected: medication.selectedDosages.length }"
                    class="list-group-item med-container-item">
<!--                  <svg v-if="medication.selectedDosages.length" class="bi bi-check-circle-fill align-baseline"-->
<!--                       height="20"-->
<!--                       viewBox="0 0 16 16" width="20" xmlns="http://www.w3.org/2000/svg">-->
<!--                    <path-->
<!--                        d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0m-3.97-3.03a.75.75 0 0 0-1.08.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-.01-1.05z"/>-->
<!--                  </svg>-->
