  </div>

</template>

<script>
import axios from "axios";
import axiosRetry from 'axios-retry';
import printJS from 'print-js'

export default {
  // eslint-disable-next-line vue/no-unused-components
  data() {
    return {
      patientName: '',
      dob: '',
      doctorName: '',
      isAdult: true,
      medications: [],
      selectedMedications: [],
      dosages: [],
      prescriptionMonths: 3,
      defaultDosage: "",
      defaultQuantity: 0,
      defaultSignature: "",
      selectedQuantity: 0,
      medicationGroup: 0,
      signature: "",
      possibleDoses: [],
      doctorsList: [],
      pdfVisible: false,
      pdf: null
    };
  },
  created() {
    this.fetchMedications();
    this.fetchDoctors();
    let urlParams = new URLSearchParams(window.location.search);
    console.log(urlParams);
    if (urlParams.size > 0) {
      this.doctorName = urlParams.get('doctorName');
      let d = urlParams.get('dob').split('.')
      this.dob = d[2] + '-' + d[1] + '-' + d[0];
      this.patientName = urlParams.get('patientName');
    }
  },
  watch: {
    "dob"() {
      this.isAdult = this.age_check()
    }
  },
  computed: {
    groupedMedications() {
      const grouped = {};
      this.medications.forEach(medication => {
        if (!grouped[medication.medicationGroup]) {
          grouped[medication.medicationGroup] = [];
        }
        grouped[medication.medicationGroup].push(medication);
      });
      return Object.values(grouped);
    },
  },
  methods: {
    age_check() {
      let dob = new Date(this.dob)
      let today = new Date()
      let of_age = new Date(today.getFullYear() - 18, today.getMonth(), today.getDay())
      return (of_age.getTime() > dob.getTime())
    },
    addCustom() {
      let newMed = {
        name: "",
        selectedDosages: [
          { dosage: "",
            prescriptionMonths: 3,
            quantity: 30,
            signature: ""}
        ],
        form: ["Tab."],

      }
      console.log(newMed)
      this.medications.customMeds.push(newMed)
    },
    fetchMedications() {
      axiosRetry(axios, {
         retries: 3, // Number of retries (Defaults to 3)
         retryDelay: axiosRetry.exponentialDelay,
         retryCondition: () => true
      });
      axios.get('http://localhost:5050/api/medications')
          .then((response) => {
            this.medications = response.data;
            this.medications.customMeds = []
            this.medications.forEach((medication) => {
              medication.dosages.map(dosage => dosage['prescriptionMonths'] = 3)
              medication.selectedDosages = [];

            });
          })
          .catch((error) => {
            console.error(error);
          });
    }, fetchDoctors() {
      axios.get('http://localhost:5050/api/doctors')
          .then((response) => {
            this.doctorsList = response.data;
          })
          .catch((error) => {
            console.error(error);
          });
    },
    submitForm() {
      this.selectedMedications = this.medications.filter((medication) => {
        return medication.selectedDosages.length
      })
      this.selectedMedications.push(...this.medications.customMeds)
      const data = {
        patientName: this.patientName,
        dob: this.dob,
        doctorName: this.doctorName,
        isAdult: this.age_check(),
        selectedMedications: this.selectedMedications.map((medication) => ({
          name: medication.name,
          form: medication.form[0],
          dosages: medication.selectedDosages,
          prescriptionMonths: medication.prescriptionMonths,
          quantity: medication.selectedQuantity,
          signature: medication.signature
        }))
      };
      axios.post('http://localhost:5050/api/print', data, { responseType: 'arraybuffer' })
          .then(response => {
            const blob = new Blob([response.data]);
            const url = URL.createObjectURL(blob);
            this.pdf = url;
            console.log(url)
            printJS(url)
            // }


          })
          .catch(error => {
            console.error(error);
          });
    }
  }
};
</script>

